import { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import Image from 'components/Image';
import Footer from 'components/Footer';
import {
    ProjectContainer,
    ProjectBackground,
    ProjectHeader,
    ProjectSection,
    ProjectSectionContent,
    ProjectImage,
    ProjectSectionHeading,
    ProjectSectionColumns,
    ProjectTextRow,
    ProjectSectionText,
} from 'components/ProjectLayout';
import { useScrollRestore } from 'hooks';
import { media } from 'utils/style';
import prerender from 'utils/prerender';
import elect_illus from 'assets/elect_illus.jpg';
import cloudMoni from 'assets/cloudMoni.png';
import cloudFull from 'assets/cloudFull.png';
import cloudFeature from 'assets/cloudFeature.svg';

const title = 'Cloud Serial';
const description =
    'Design and development of the full end to end IoT framework for the devices manufactured at M-Kris Electric.';
const roles = [
    'Full Stack Development',
    'Vanilla JS,HTML,CSS and Nodejs',
    'Internet of Things'
];

const ProjectDM = () => {
    useScrollRestore();

    return (
        <Fragment>
            <Helmet>
                <title>Projects | {title}</title>
                <meta name="description" content={description} />
            </Helmet>
            <ProjectContainer>
                <ProjectBackground
                    srcSet={`${elect_illus} 1080w, ${elect_illus} 2160w`}
                    placeholder={elect_illus}
                    entered={!prerender}
                />
                <ProjectHeader
                    title={title}
                    description={description}
                    url="https://mkrisproject.appspot.com/"
                    roles={roles}
                />
                <ProjectSection first>
                    <ProjectSectionContent>
                        <ProjectImage
                            raised
                            srcSet={`${cloudMoni} 1280w, ${cloudMoni} 2560w`}
                            placeholder={cloudMoni}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 800px, 1000px`}
                            alt="Device Models plugin interface."
                        />
                    </ProjectSectionContent>
                </ProjectSection>
                <ProjectSection>
                    <ProjectTextRow>
                        <ProjectSectionHeading>The Aim</ProjectSectionHeading>
                        <ProjectSectionText>
                            To Create an end to end IoT framework for the all the monitoring devices
                            M-Kris Electric makes. The main purpose was to connect the non so smart devices
                            to the internet for the real time data monitoring and analytics of the
                            devices which have been installed on various sites.
                        </ProjectSectionText>
                        <ProjectSectionText>
                            Both the hardware and software for the monitoring loggers have been developed
                            by me. The loggers come in various various types based on their communication type
                            and the location types:
                            <ul>
                                <li>GSM based Loggers for remote locations</li>
                                <li>Ethernet Based Loggers</li>
                                <li>Wifi Based Loggers</li>
                                <li>Offline local area based Loggers</li>
                            </ul>
                        </ProjectSectionText>
                        <ProjectSectionText>
                            The website also features control over the devices and also gives basic
                            analytics of the data retrieved from the loggers.
                        </ProjectSectionText>
                    </ProjectTextRow>
                </ProjectSection>
                <ProjectSection light>
                    <ProjectSectionColumns>
                        <ProjectTextRow>
                            <ProjectSectionHeading>Features</ProjectSectionHeading>
                            <ProjectSectionText>
                                <ul>
                                    <li>Live status of the installation – available on demand anywhere in world</li>
                                    <li>No need to maintain manual log sheets</li>
                                    <li>No need to visit individual devices for data collection</li>
                                    <li>Historical data – at the click of a button</li>
                                    <li>Data presented in easily readable tables, graphs</li>
                                    <li>Option to remotely switch on/off the connected devices, view/change their settings</li>
                                    <li>Easily scalable – easy addition/removal of devices</li>
                                    <li>Opportunity to save on energy costs by employing machine-learning/AI-assisted techniques</li>
                                </ul>
                            </ProjectSectionText>
                        </ProjectTextRow>
                        <Image
                            srcSet={`${cloudFeature} 400w, ${cloudFeature} 898w`}
                            placeholder={cloudFeature}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 100vw, 50vw`}
                            alt="The Device Models color palette and logo, featuring a low poly monogram to convey its 3D allure."
                        />
                    </ProjectSectionColumns>
                </ProjectSection>
                <ProjectSection>
                    <ProjectSectionContent>
                        <Image
                            srcSet={`${cloudFull} 400w, ${cloudFull} 898w`}
                            placeholder={cloudFull}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 100vw, 50vw`}
                            alt="The Device Models color palette and logo, featuring a low poly monogram to convey its 3D allure."
                        />
                    </ProjectSectionContent>
                </ProjectSection>
            </ProjectContainer>
            <Footer />
        </Fragment>
    );
};

export default ProjectDM;
